import React, { useState } from 'react'
import cls from './DocumentPage.module.css' //  CSS-
import FieldInput from 'components/FieldInput/FieldInput'

const DocumentPage = () => {
  const [isEdit, setIsEdit] = useState(false)
  const [docData, setDocData] = useState({
    title: '  ',
    author: ' ',
    num: '123-',
    date: '2025-08-20',
    status: '',
    kind: ' ',
    description: ' ...',
    file: '/files/law.pdf'
  })

  const handleChange = (field, value) => {
    setDocData(prev => ({ ...prev, [field]: value }))
  }

  return (
    <>
      <div className={cls.document}>
        <div className={cls.documentInfo}>
          <h4></h4>
        </div>

        <div className={cls.documentInner}>
          <FieldInput
            isEdit={isEdit}
            type="input"
            title=" "
            value={docData.title}
            onChange={val => handleChange('title', val)}
          />

          <FieldInput
            isEdit={isEdit}
            type="select"
            title=" "
            value={docData.author}
            options={[' ', '', '']}
            onChange={val => handleChange('author', val)}
          />

          <FieldInput
            isEdit={isEdit}
            type="input"
            title=" "
            value={docData.num}
            onChange={val => handleChange('num', val)}
          />

          <FieldInput
            isEdit={isEdit}
            type="input"
            inputType="date"
            title=" "
            value={docData.date}
            onChange={val => handleChange('date', val)}
          />

          <FieldInput
            isEdit={isEdit}
            type="select"
            title=""
            value={docData.status}
            options={['', ' ', ' ']}
            onChange={val => handleChange('status', val)}
          />

          <FieldInput
            isEdit={isEdit}
            type="select"
            title=" "
            value={docData.kind}
            options={[' ', '', '']}
            onChange={val => handleChange('kind', val)}
          />

          <FieldInput
            isEdit={isEdit}
            type="textarea"
            title=""
            value={docData.description}
            onChange={val => handleChange('description', val)}
          />

          <div className={cls.documentFiles}>
            <h4></h4>
            {docData.file ? (
              <a href={docData.file} className={cls.docLink} target="_blank" rel="noreferrer">
                 
              </a>
            ) : (
              <span id="filePlaceholder">...</span>
            )}
          </div>
        </div>
      </div>

      <div className={cls.documentControlPanel}>
        <button className={cls.blueBtn}></button>
        <div id="admin-controls">
          {!isEdit ? (
            <button className={cls.blueBtn} onClick={() => setIsEdit(true)}></button>
          ) : (
            <>
              <button className={cls.blueBtn} onClick={() => setIsEdit(false)}></button>
              <button className={cls.blueBtn} onClick={() => setIsEdit(false)}></button>
            </>
          )}
          <button className={cls.redBtn}></button>
        </div>
      </div>
    </>
  )
}

export default DocumentPage
