import { useState } from 'react'
import cls from './NewPage.module.css'
import FieldInput from 'components/FieldInput/FieldInput'

const NewPage = () => {
  const [post, setPost] = useState({
    title: "   SARS-CoV-2   ",
    content: "     . ..     15  2025 . (07.04.25 - 13.04.25)               10,1%   63,0  10 000 ,     (89,9)  29,9%       35,0%  15         12851    SARS-CoV-2    40 (0,3%) .       COVID-19    2025         JN.1.16    (69,4%    ); 16,7%    LF.7   .    (  -  2025 .)     ,      (VUM): KP.2, KP.3, KP.3.1.1, JN.1.18, LB.1, XEC, LP.8.1.         2024 .    VUM        2025         6,5%.   KP.2  1,8%    2,2 %   2025 .   XEC  3,3% -    2,1% -  .   KP.3, KP.3.1.1  JN.1.18   1%      SARS-CoV-2   .      LP.8.1    :  1% -    6,5%   2025 .    LP.8.1          VUM       (  XEC).   ,     ,   LP.8.1,      . ,     COVID-19            ,     LP.8.1   XEC, ,   ,      -  JN.1  KP.2.  ,  ,                  . : https://www.influenza.spb.ru/surveillance/flu-bulletin// https://www.who.int/publications/m/item/risk-evaluation-for-sars-cov-2-variant-under-monitoring-lp81",
    pubdate: "2025-08-25"
  })

  const [draftPost, setDraftPost] = useState(post)
  const [isEdit, setIsEdit] = useState(false)

  const handleChange = (field) => (val) => {
    setDraftPost(prev => ({ ...prev, [field]: val }))
  }

  const handleEdit = () => {
    setDraftPost(post)
    setIsEdit(true)
  }

  const handleCancel = () => {
    setDraftPost(post)
    setIsEdit(false)
  }

  const handleSave = () => {
    setPost(draftPost)
    setIsEdit(false)
  }

  return (
    <div className={cls.pageWrapper}>
      <div className={cls.postInfo}>
        <h4 className={cls.postTitle}>
          <FieldInput
            isEdit={isEdit}
            type="input"
            value={draftPost.title}
            onChange={handleChange('title')}
          />
        </h4>

        <div className={cls.postContent}>
          <FieldInput
            isEdit={isEdit}
            type="textarea"
            value={draftPost.content}
            onChange={handleChange('content')}
          />
        </div>

        <span className={cls.postPubdate}>
          <b> : </b>
          <FieldInput
            isEdit={isEdit}
            type="input"
            value={draftPost.pubdate}
            onChange={handleChange('pubdate')}
          />
        </span>
      </div>

      <div className={cls.postControlPanel}>
        <button className={cls.blueBtn}></button>
        <div className={cls.adminControls}>
          {isEdit ? (
            <>
              <button className={cls.blueBtn} onClick={handleCancel}></button>
              <button className={cls.blueBtn} onClick={handleSave}></button>
            </>
          ) : (
            <button className={cls.blueBtn} onClick={handleEdit}></button>
          )}
          <button className={cls.redBtn}></button>
        </div>
      </div>
    </div>
  )
}

export default NewPage
