import { useNavigate } from 'react-router-dom'
import cls from './MicrofloraCard.module.css'

const MicrofloraCard = ({ flora }) => {
  const {
    organization,
    kind,
    view,
    name,
    culture_type,
    culture_props,
    enzyme_activity,
    antigenic_structure,
    stability_profile,
    specific_activity,
    other,
    s_rrna,
    sequencing,
    object_of_symbiosis,
  } = flora

  const navigate = useNavigate()
  
  return (
    <tr className={cls.customRow} onClick={() => navigate('/open-microflora/1')}>
        <td>{organization || ' '}</td>
        <td>{kind || ' '}</td>
        <td>{view || ' '}</td>
        <td>{name || ' '}</td>
        <td>{culture_type || ' '}</td>
        <td>{culture_props || ' '}</td>
        <td>{enzyme_activity || ' '}</td>
        <td>{antigenic_structure || ' '}</td>
        <td>{stability_profile || ' '}</td>
        <td>{specific_activity || ' '}</td>
        <td>{other || ' '}</td>
        <td>{s_rrna || ' '}</td>
        <td>{sequencing || ' '}</td>
        <td>{object_of_symbiosis || ' '}</td>
      </tr>
  )
}

export default MicrofloraCard