import React, { useState } from 'react'
import cls from './FloraPage.module.css'
import { useLocation } from 'react-router-dom'
import FieldInput from 'components/FieldInput/FieldInput'

const FloraPage = () => {
  const [flora, setFlora] = useState({
    organization: ' ',
    creationDate: '2025-08-28',
    name: 'Lactobacillus (123)',
    inBook: ' A',
    kind: ' B',
    objectOfSymbiosis: ' X',
    view: ' Y',
    cultureType: ' Z',
    cultureProps: '',
    enzymeActivity: '',
    antigenicStructure: '',
    stabilityProfile: '',
    specificActivity: '',
    other: '. ',
    rrna: '',
    sequencing: ''
  })

  const { history } = useLocation()

  const [isEdit, setIsEdit] = useState(false)

  const handleChange = (field, value) => {
    setFlora(prev => ({ ...prev, [field]: value }))
  }

  return (
    <div className={cls.floraInfo}>
      <div className={cls.floraTitle}>
        <h4>   </h4>
      </div>

      <div>
        <div className={cls.floraInfoRow}>
          <div className={cls.floraField}>
            <FieldInput
              title=""
              type="input"
              value={flora.organization}
              isEdit={isEdit}
              onChange={val => handleChange('organization', val)}
            />
          </div>
          <div className={cls.floraField}>
            <FieldInput
              title=" "
              type="input"
              value={flora.creationDate}
              isEdit={isEdit}
              onChange={val => handleChange('creationDate', val)}
            />
          </div>
        </div>
        <div className={cls.floraInfoRow}>
          <div className={cls.floraField}>
            <FieldInput
              title=" ()"
              type="input"
              value={flora.name}
              isEdit={isEdit}
              onChange={val => handleChange('name', val)}
              />
          </div>
        </div>

        <div className={cls.floraInfoRow}>
          <div className={cls.floraField}>
            <FieldInput
              title=""
              type="select"
              value={flora.inBook}
              options={[' A', ' B']}
              isEdit={isEdit}
              onChange={val => handleChange('inBook', val)}
              />
          </div>
        </div>

        <div className={cls.floraInfoRow}>
          <div className={cls.floraField}>
            <FieldInput
              title=""
              type="select"
              value={flora.kind}
              options={[' A', ' B']}
              isEdit={isEdit}
              onChange={val => handleChange('kind', val)}
            />
          </div>
          <div className={cls.floraField}>
            <FieldInput
              title="-"
              type="select"
              value={flora.objectOfSymbiosis}
              options={[' X', ' Y']}
              isEdit={isEdit}
              onChange={val => handleChange('objectOfSymbiosis', val)}
            />
          </div>
        </div>

        <div className={cls.floraInfoRow}>
          <div className={cls.floraField}>
            <FieldInput
              title=""
              type="select"
              value={flora.view}
              options={[' X', ' Y']}
              isEdit={isEdit}
              onChange={val => handleChange('view', val)}
            />
          </div>
          <div className={cls.floraField}>
            <FieldInput
              title=" "
              type="select"
              value={flora.cultureType}
              options={[' X', ' Y']}
              isEdit={isEdit}
              onChange={val => handleChange('cultureType', val)}
            />
          </div>
        </div>

        <div className={cls.floraInfoRow}>
            <div className={cls.phenotypicCharacters}>
            <h5> </h5>
            <FieldInput
              title="- "
              type="radio"
              value={flora.cultureProps}
              options={['','']}
              isEdit={isEdit}
              onChange={val => handleChange('cultureProps', val)}
            />
            <FieldInput
              title="  "
              type="radio"
              value={flora.enzymeActivity}
              options={['','']}
              isEdit={isEdit}
              onChange={val => handleChange('enzymeActivity', val)}
            />
            <FieldInput
              title=" "
              type="radio"
              value={flora.antigenicStructure}
              options={['','']}
              isEdit={isEdit}
              onChange={val => handleChange('antigenicStructure', val)}
            />
            <FieldInput
              title=" / "
              type="radio"
              value={flora.stabilityProfile}
              options={['','']}
              isEdit={isEdit}
              onChange={val => handleChange('stabilityProfile', val)}
            />
            <FieldInput
              title=" "
              type="radio"
              value={flora.specificActivity}
              options={['','']}
              isEdit={isEdit}
              onChange={val => handleChange('specificActivity', val)}
            />
            <FieldInput
              title=" ()"
              type="input"
              value={flora.other}
              isEdit={isEdit}
              onChange={val => handleChange('other', val)}
            />
          </div>

          <div className={cls.genotypicCharacters}>
            <h5> </h5>
            <FieldInput
              title="16S "
              type="radio"
              value={flora.rrna}
              options={['','']}
              isEdit={isEdit}
              onChange={val => handleChange('rrna', val)}
            />
            <FieldInput
              title=""
              type="radio"
              value={flora.sequencing}
              options={['','']}
              isEdit={isEdit}
              onChange={val => handleChange('sequencing', val)}
            />
          </div>
        </div>
      </div>

      <div className={cls.floraControlPanel}>
        <button className={cls.blueBtn} onClick={() => history.back()}></button>
        <div>
          {isEdit ? (
            <>
              <button className={cls.blueBtn} onClick={() => setIsEdit(false)}></button>
              <button className={cls.blueBtn} onClick={() => { console.log(flora); setIsEdit(false) }}></button>
            </>
          ) : (
            <button className={cls.blueBtn} onClick={() => setIsEdit(true)}></button>
          )}
          <button className={cls.redBtn}></button>
        </div>
      </div>
    </div>
  )
}

export default FloraPage
